@import "variables";

.slider {
  &__button {
    width: 81px;
    height: 64px;
    /* stylelint-disable */
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%), #fcc304;
    /* stylelint-enable */
    color: $whiteColor;

    &--left {
      position: absolute;
      top: 288px;
      left: 0;
      border-radius: 0 50px 50px 0;
    }

    &--right {
      position: absolute;
      top: 288px;
      left: -81px;
      border-radius: 50px 0 0 50px;
    }
  }
  &__wrapper {
    position: relative;
  }
  &__title {
    font-style: normal;
    font-weight: 800;
    font-size: 12.6px;
    line-height: 150%;
    display: flex;
    align-items: center;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #56ccf2;
    position: absolute;
    left: 98px;
    top: 477px;
  }
  &__description {
    font-style: normal;
    font-weight: 900;
    font-size: 19.6px;
    line-height: 140%;
    color: $whiteColor;
    position: absolute;
    top: 500px;
    left: 98px;
  }
  &__img {
    height: 640px;
    width: 1146px;
  }
  &__container {
    width: 1146px;
    height: 100%;
    position: relative;
    overflow: hidden;
  }
  &__inner {
    display: flex;
    position: absolute;
    left: 0;
    overflow: hidden;
    transition: left 2s ease;
  }
}
